/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before, .slick-track:after {
    content: "";
    display: table; }
  .slick-track:after {
    clear: both; }
  .slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

/* Slider */
.slick-loading .slick-list {
  background: #fff url("../img/ajax-loader.gif") center center no-repeat; }

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 46px;
  width: 46px;
  top: 50%;
  z-index: 10;
  font-size: 0;
  border: none;
  outline: none;
  line-height: 0;
  cursor: pointer;
  background: url("../img/arrow_left.png") no-repeat center;
  transform: translate(0, -50%); }
  @media (max-width: 991px) {
    .slick-prev,
    .slick-next {
      border: 1px solid #ddd; } }

.slick-prev {
  left: -6rem; }
  [dir="rtl"] .slick-prev {
    left: auto;
    right: -6rem; }
  @media (max-width: 991px) {
    .slick-prev {
      left: -2rem; } }

.slick-next {
  right: -6rem;
  background-image: url("../img/arrow_right.png"); }
  [dir="rtl"] .slick-next {
    left: -6rem;
    right: auto; }
  @media (max-width: 991px) {
    .slick-next {
      right: -2rem; } }

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 0; }

.slick-dots {
  position: absolute;
  display: block;
  margin: 0;
  padding: 0;
  bottom: 3rem;
  list-style: none;
  text-align: center; }
  .slick-dots li {
    display: inline-block;
    position: relative;
    height: 2rem;
    width: 2rem;
    margin: 0 3px;
    cursor: pointer; }
    .slick-dots li button {
      display: block;
      font-size: 0;
      border: 0;
      width: 2rem;
      height: 2rem;
      padding: 5px;
      outline: none;
      line-height: 0;
      cursor: pointer;
      color: transparent;
      background: transparent;
      display: none; }
      .slick-dots li button:hover, .slick-dots li button:focus {
        outline: none; }
        .slick-dots li button:hover:before, .slick-dots li button:focus:before {
          opacity: 1; }
      .slick-dots li button:before {
        position: absolute;
        top: 0;
        left: 0;
        content: "•";
        width: 2rem;
        height: 2rem;
        font-family: "slick";
        font-size: 6px;
